import Toast from 'wxcomponents/vant/dist/toast/toast';
export default {
	data() {
		return {
			id:'',
			collection:'',
			product:'',
			detail: [],
			show: false,
			size: ["60*40cm", "58*36cm"],
			color: ["白色", "红色"],
			curSize: null,
			curColor: null,
			curIndex: null,
			dataList: [],
			count: 1,
			Operations:'',
			newsList:[],
			productList:[]
		}
	},
	methods: {
		goBack(){
			uni.navigateBack()
		},
		isShow(e){
			this.show=true
			// console.log(e.currentTarget.dataset.index)
			this.Operations=e.currentTarget.dataset.index
		},
		onClose(){
			this.show=false
		},
		// 尺码选择
		selectSize(e){
			this.curSize=e.currentTarget.dataset.size
		},
		// 颜色选择
		selectColor(e){
			this.curColor=e.currentTarget.dataset.color
		},
		// 加号按钮
		add(){
			this.count++
		},
		// 减号按钮
		cutBack(){
			this.count--
			if(this.count<=1){
				this.count=1
			}
		},
		// 跳转首页
		goIndex(){
			uni.switchTab({
				url:'/pages/index/index'
			})
		},
		// 商品收藏  解决收藏问题
		collect(e){
			this.collection=!this.collection
			var arr=this.newsList
			for(var i=0;i<arr.length;i++){
				if(arr[i].id==e.currentTarget.dataset.id){
					arr[i].collect=this.collection
					uni.setStorage({
						key:'collectList',
						data:JSON.stringify(arr)
					})
				}
			}
		},
		// 确定按钮(加入购物车功能)
		goShop(){
			if(this.size[this.curSize]&&this.color[this.curColor]){
				
				
				if(this.Operations==0){
					var product={
						img:this.product.img,
						title:this.product.title,
						info:this.product.material,
						count:this.count,
						price:this.product.price,
						allPrice:this.count*this.product.price,
						size:this.size[this.curSize],
						color:this.color[this.curColor],
						check:true
					}
					uni.setStorage({
						key:'cart',
						data:JSON.stringify([product])
					})
					wx.navigateTo({
						url:'/pages/settlement/settlement'
					})
				}else{
					var product={
						img:this.product.img,
						title:this.product.title,
						info:this.product.material,
						count:this.count,
						price:this.product.price,
						allPrice:this.count*this.product.price,
						size:this.size[this.curSize],
						color:this.color[this.curColor],
						check:false
					}
					this.productList.push(product)
					uni.setStorage({
						key:'cart',
						data:JSON.stringify(this.productList)
					})
					wx.switchTab({
						url:'/pages/shop/shop'
					})
				}
			}else{
				Toast('请选择商品类型');
			}
		},
	},
	onLoad(options){
		var _this=this
		this.product=JSON.parse(options.item)
		this.id=this.product.id
		// uni.getStorage({
		// 	key:'cart',
		// 	success(e) {
		// 		_this.productList=JSON.parse(e.detail)
		// 	}
		// })
	},
	onShow(){
		var that=this
		// 判断是否已收藏该id下的商品
		uni.getStorage({
			key:'collectList',
			success(e){
				// console.log(JSON.parse(e.data))
				that.newsList=JSON.parse(e.data)||[]
				// console.log(that.newsList)
				for(var i=0;i<that.newsList.length;i++){
					if(that.newsList[i].id==that.id){
						that.collection=that.newsList[i].collect
					}
				}
			}
		})
		uni.getStorage({
			key:'cart',
			success(e){
				that.productList=JSON.parse(e.data)||[]
			}
		})
	}
}
